<template>
  <div class="card">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Maintenance Progress</span> &nbsp;
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--back"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAll"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body" style="min-height: 230px;">
      <div>
        <ACCFormList
          :prop="propList"
          :title="''"
          @rowClicked="rowClicked"
          ref="ref_list"
          @onRenderData="onRenderData"
        >
        <template slot="status" slot-scope="data">
          {{ data.item.status}}
          <b-badge v-if="data.item.maintenance_status == 'P'" variant="success">&nbsp;</b-badge>
          <b-badge v-if="data.item.maintenance_status == 'Q'" variant="unscheduled">&nbsp;</b-badge>
        </template>
        </ACCFormList>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/MM/MM_FleetMaintProgress",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 6,
      },
    };
  },
  computed: {},
  methods: {
    doViewAll() {
      this.$router.push({ name: "MM_FleetMaintenanceProgress" });
    },
    rowClicked(record, index) {
      var param = record;
      param.isEdit = true;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_FleetMaintenanceProgressForm" });
    },

    RenderData() {
      this.$refs.ref_list.doGetList("");
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>